@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,700&display=swap);
* {
	box-sizing: border-box;
}

html {
	font-family: "Open Sans", sans-serif;
}

body {
	margin: 0;
	background-color: #4d4d4d;
}

.slide-in-left-enter {
	-webkit-transform: translateX(-100%);
	        transform: translateX(-100%);
}

.slide-in-left-enter-active {
	-webkit-transform: translateX(0);
	        transform: translateX(0);
	opacity: 1;
	transition: all 200ms;
}

.slide-in-left-exit {
	-webkit-transform: translateX(0%);
	        transform: translateX(0%);
	opacity: 1;
}

.slide-in-left-exit-active {
	-webkit-transform: translateX(-100%);
	        transform: translateX(-100%);
	opacity: 0;
	transition: all 200ms;
}

.center {
	text-align: center;
	display: flex;
	justify-content: center;
	align-items: center;
}


.backdrop {
	width: 100%;
	height: 100vh;
	position: fixed;
	top: 0;
	left: 0;
	background-color: rgba(0, 0, 0, 0.75);
	z-index: 10;
}


.drawer {
	width: 70%;
	height: 100vh;
	position: fixed;
	top: 0;
	left: 0;
	background-color: white;
	box-shadow: 0 2px 8px rgba(0, 0, 0, 0.26);
	z-index: 100;
}

.drawer-nav {
	height: 100%;
}


.nav-links {
	width: 100%;
	height: 100%;
	margin: 0;
	padding: 0;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	list-style: none;
}

.nav-links li {
	margin: 1rem;
}

.nav-links .nav-link {
	padding: 0.5rem;
	color: #292929;
	border: 1px solid transparent;
	border-radius: 6px;
	text-decoration: none;
}

.nav-links .nav-link:hover,
.nav-links .nav-link:active,
.nav-links .nav-link.active {
	color: #292929;
	background-color: #ffd900;
	/* border-color: #292929; */
}

.nav-links button {
	padding: 0.5rem;
	color: #292929;
	background-color: transparent;
	border: 1px solid #292929;
	font: inherit;
	cursor: pointer;
}

.nav-links button:focus {
	outline: none;
}

.nav-links button:hover,
.nav-links button:active {
	color: white;
	background-color: #292929;
}

@media (min-width: 768px) {
	.nav-links {
		flex-direction: row;
	}

	.nav-links li {
		margin: 0 0.5rem;
	}

	.nav-links .nav-link {
		color: white;
		text-decoration: none;
	}

	.nav-links button {
		color: white;
		background-color: transparent;
		border: 1px solid white;
	}

	.nav-links button:hover,
	.nav-links button:active {
		color: #292929;
		background-color: #ffd900;
	}
}


.header {
	width: 100%;
	height: 4rem;
	padding: 0 1rem;
	position: fixed;
	top: 0;
	left: 0;
	display: flex;
	align-items: center;
	background-color: #ff0055;
	box-shadow: 0 2px 6px rgba(0, 0, 0, 0.26);
	z-index: 5;
}

main {
	margin-top: 5rem;
}

.header__menu-btn {
	width: 3rem;
	height: 3rem;
	background-color: transparent;
	border: none;
	display: flex;
	flex-direction: column;
	justify-content: space-around;
	margin-right: 2rem;
	cursor: pointer;
}

.header__menu-btn span {
	display: block;
	width: 3rem;
	height: 2.5px;
	background-color: white;
}

.header__title {
	color: white;
}

.header__title a {
	color: white;
	text-decoration: none;
}

@media (min-width: 768px) {
	.header {
		justify-content: space-between;
	}

	.header__menu-btn {
		display: none;
	}
}


.header-nav {
	display: none;
}

@media (min-width: 768px) {
	.header-nav {
		display: block;
	}
}


.spinner-dual-ring {
	display: inline-block;
	width: 64px;
	height: 64px;
}

.spinner-dual-ring:after {
	content: " ";
	display: block;
	width: 46px;
	height: 46px;
	margin: 1px;
	border-radius: 50%;
	border: 5px solid #510077;
	border-color: #510077 transparent #510077 transparent;
	-webkit-animation: spinner-dual-ring 1.2s linear infinite;
	        animation: spinner-dual-ring 1.2s linear infinite;
}

.spinner__overlay {
	width: 100%;
	height: 100%;
	position: absolute;
	top: 0;
	left: 0;
	background: rgba(255, 255, 255, 0.9);
	display: flex;
	justify-content: center;
	align-items: center;
}

@-webkit-keyframes spinner-dual-ring {
	0% {
		-webkit-transform: rotate(0deg);
		        transform: rotate(0deg);
	}
	100% {
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg);
	}
}

@keyframes spinner-dual-ring {
	0% {
		-webkit-transform: rotate(0deg);
		        transform: rotate(0deg);
	}
	100% {
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg);
	}
}

.form {
	width: 90%;
	max-width: 40rem;
	margin: 0 auto;
	padding: 1rem;
	background-color: white;
	border-radius: 6px;
	box-shadow: 0 2px 8px rgba(0, 0, 0, 0.26);
	list-style: none;
}


